import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRoomState, IRoomVM, IDeviceErrorState, IDeviceState } from './heating-control.model';
import { tap, switchMap } from 'rxjs/operators';
import { Observable, merge } from 'rxjs';
import {SelectionModel} from '@angular/cdk/collections';
import { MatSliderChange } from '@angular/material/slider';


@Component({
  templateUrl: './heating-control.component.html',
  styleUrls: ['./heating-control.component.scss']
})
export class HeatingControlComponent {
  displayedColumns: string[] = ['selection', 'name', 'temperature', 'action'];
  rooms: IRoomVM[] = [];
  sliderValue = 0;
  selection = new SelectionModel<IRoomVM>(true, []);
  isSliderDisabled = true;

  constructor(private http: HttpClient) {
    this.initRooms();
  }

  changeSelection(room: IRoomVM): void {
    this.selection.clear()
    this.selection.select(room);
    this.isSliderDisabled = false;
    this.sliderValue = this.rooms.find((r: IRoomVM) => r.name === room.name)?.temperature || 0;
  }

  sliderChanged(event: MatSliderChange): void {
    const name: string = this.selection.selected[0].name;
    const temperature: number = event.value as number;
    const room = this.rooms.find((room: IRoomVM) => room.name === name) as IRoomVM;

    room.inSync = false;
    
    this.http.post<IRoomState>('heating-control/set-temp', { name, temperature }).subscribe((roomState: IRoomState) => {
      const room = this.rooms.find((room: IRoomVM) => roomState.name === room.name) as IRoomVM;
      const hasError = roomState.devices.some((device: any) => device.errorMsg);

      room.temperature = hasError ? undefined : this.getMaxTemperature(roomState.devices);
      room.inSync = true;
    });
  }

  trackByName(index: number, room: IRoomVM): string {
    return room.name;
  }

  setEcoMode(event: Event, name: string): void {
    const room = this.rooms.find((room: IRoomVM) => room.name === name) as IRoomVM;

    room.inSync = false;
    
    this.http.post<IRoomState>('heating-control/set-eco', { name }).subscribe((roomState: IRoomState) => {
      const room = this.rooms.find((room: IRoomVM) => roomState.name === room.name) as IRoomVM;
      const hasError = roomState.devices.some((device: any) => device.errorMsg);

      room.temperature = hasError ? undefined : this.getMaxTemperature(roomState.devices);
      room.inSync = true;
    });
    event.stopPropagation();
  }

  setComfortMode(event: Event, name: string): void {
    const room = this.rooms.find((room: IRoomVM) => room.name === name) as IRoomVM;

    room.inSync = false;
    
    this.http.post<IRoomState>('heating-control/set-comfort', { name }).subscribe((roomState: IRoomState) => {
      const room = this.rooms.find((room: IRoomVM) => roomState.name === room.name) as IRoomVM;
      const hasError = roomState.devices.some((device: any) => device.errorMsg);

      room.temperature = hasError ? undefined : this.getMaxTemperature(roomState.devices);
      room.inSync = true;
    });
    event.stopPropagation();
  }

  setOff(event: Event, name: string): void {
    const room = this.rooms.find((room: IRoomVM) => room.name === name) as IRoomVM;

    room.inSync = false;
    
    this.http.post<IRoomState>('heating-control/set-off', { name }).subscribe((roomState: IRoomState) => {
      const room = this.rooms.find((room: IRoomVM) => roomState.name === room.name) as IRoomVM;
      const hasError = roomState.devices.some((device: any) => device.errorMsg);

      room.temperature = hasError ? undefined : this.getMaxTemperature(roomState.devices);
      room.inSync = true;
    });
    event.stopPropagation();
  }

  private initRooms(): void {
    this.http.get<string[]>('heating-control/rooms').pipe(
      tap((rooms: string[]) => this.rooms = rooms.map((name: string) => ({name}))),
      switchMap((rooms: string[]) => merge(...rooms.map((room: string) => this.getRoomState(room))))
    ).subscribe((roomState: IRoomState) => {
      const room = this.rooms.find((room: IRoomVM) => roomState.name === room.name) as IRoomVM;
      const hasError = roomState.devices.some((device: any) => device.errorMsg);

      room.mode = roomState.mode;
      room.temperature = hasError ? undefined : this.getMaxTemperature(roomState.devices);
      room.lowBattery = this.hasLowBattery(roomState.devices);
      room.inSync = true;
    });
  }

  private getRoomState(name: string): Observable<IRoomState> {
    return this.http.get<IRoomState>(`heating-control/rooms?name=${name}`);
  }

  private getMaxTemperature(devices: (IDeviceState | IDeviceErrorState)[]): number {
    return devices.reduce((acc: number, device: any) => {
      const temperature = device.temperature || 0;

      return temperature > acc ? temperature : acc;
    }, 0);
  }

  private hasLowBattery(devices: (IDeviceState | IDeviceErrorState)[]): boolean {
    return devices.some((device: any) => {
      return device.lowBattery ?? false;
    });
  }
}
