import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HeatingControlComponent } from './heating-control/heating-control.component';
import { ServerStatusComponent } from './server-status/server-status.component';
import { ServerStatusGuard } from './server-status/server-status.guard';

const routes: Routes = [
  { 
    path: 'heating-control', 
    component: HeatingControlComponent, 
    canActivate: [
      ServerStatusGuard
    ]
  },
  { path: '**', component: ServerStatusComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
