import { Component } from "@angular/core";
import { ServerStatusService } from './server-status.service';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { HttpClient } from '@angular/common/http';
import { IServerStatus } from './server-status.model';

@Component({
    templateUrl: "./server-status.component.html",
    styleUrls: ["./server-status.component.scss"]
})
export class ServerStatusComponent {

    isRunning = false;
    inProgress = true;

    constructor(
        private serverStatusService: ServerStatusService,
        private http: HttpClient
    ) {

        this.serverStatusService.fetchServerStatus().then((isRunning: boolean) => {
            this.isRunning = isRunning;
            this.inProgress = false;
        });
    }

    toggleState(event: MatSlideToggleChange): void {
        const route: string = event.checked ? "startup" : "shutdown";

        this.inProgress = true;
        this.http.get<IServerStatus>(route).subscribe((serverStatus: IServerStatus) => {
            this.isRunning = serverStatus.isRunning;
            this.inProgress = false;
        });
    }
}