import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'temperature'})
export class TemperaturePipe implements PipeTransform {
  transform(value: number|undefined): string {
    if (value === undefined) {
        return "-";
    } else if (value === 4.5) {
        return "OFF"
    } else {
        return `${value} °C`;
    }
  }
}